<template>
    <div class="register">
        <section class="form_container">
            <div class="manage_tip">
                <span class="title">OA</span>
                <!-- element form   rules="rules" -->
                <el-form :model="registerUser"  :rules="rules" ref="registerForm" label-width="80px" class="registerForm">
                    
                    <el-form-item label="" prop="name">
                        <el-input  v-model="registerUser.name" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="email">
                        <el-input  v-model="registerUser.email" placeholder="email"></el-input>
                    </el-form-item>
                    
                    <el-form-item label="" prop="password">
                        <el-input type="password" v-model="registerUser.password" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="password2">
                        <el-input type="password"  v-model="registerUser.password2" placeholder=""></el-input>
                    </el-form-item>
                    
                    <el-form-item label="" >
                        <el-select  v-model="registerUser.identity" placeholder="">
                            <el-option label="" value="manager"></el-option>
                            <el-option label="" value="employee"></el-option>
                        </el-select>
                    </el-form-item>
                    
                    <el-form-item>
                        <el-button type="primary" class="submit_btn"  @click="submitForm('registerForm')"></el-button>
                        
                    </el-form-item>
                </el-form>
            </div>
        </section>
    </div>
</template>
<script>
export default {
    name:"register",
    components:{},
    data(){
        // 
        var validatePass2 = (rule, value, callback) => {
      
         if (value !== this.registerUser.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      //
        return{
            registerUser:{
                name:'',
                email:'',
                password:'',
                password2:'',
                identity:''
            },
            // 
            rules:{
                name:[
                    //required 
                    {required:true,message:"",trigger:"blur"},
                    {min:2,max:30,message:"230",trigger:'blur'}
                ],
                email:[
                    {required: true, message: "", trigger: 'blur' },
                    {type:'email',required:true,message:"",trigger:"blur"}
                ],
                password:[
                    {required:true,message:"",trigger:"blur"},
                    {min:6,max:30,message:"630",trigger:"blur"}
                ],
                password2:[
                    {required:true,message:"",trigger:"blur"},
                    {min:6,max:30,message:"630",trigger:"blur"},
                    {validator:validatePass2,trigger:"blur"}
                ]
            }
        }
    },
    methods:{
        //
        submitForm(formName){

            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios.post('/api/users/register',this.registerUser).then(res => {
                        //
                        this.$message({
                            message:"",
                            type:'success'
                        });

                        
                    });
                    this.$router.push('/login');
                } 
            });
        
        
        }
    
    },


};
</script>
<style  scoped>
.register {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(../assets/bg.jpg) no-repeat center center;
  background-size: 100% 100%;
}
.form_container {
  width: 370px;
  height: 210px;
  position: absolute;
  top: 10%;
  left: 34%;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}
.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.registerForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}

.submit_btn {
  width: 100%;
}
</style>




