<template>
    <div>
    
    <div id="visualization">
      <div class="vis-network" >
      </div> 
    </div>
    </div>
</template>

<script>
//
import{DataSet,Network}from'vis/index-network';

exportdefault{
name:'VisNetwork',
data(){
return{
network:null
}
},
mounted(){
this.create();
},
methods:{
  handleClick(params){
    this.$router.push({
            path: '/index2',
            query:{
              id: params.nodes[0]
            }
          })
  }
  ,
create(){
//createanarraywithnodes
varnodes=newDataSet([
         {id:'IP ',
           label:'IP ',
           level:0,
           font:{size:20},
           size:50,
           image: require("../assets/img/internet.png"),
           shape:"image",
           title: ""
           },
{id:'gdcni18',
           label:'gdcni18'+'\n'+'',
           level:1,
           font:{size:20},
           size:50,
           image: require("../assets/img/computer.png"),
           shape:"image",
           title: ""
           },
{id:'gdcni12',
           label:'gdcni12'+'\n'+'',
           level:2,
           font:{size:20},
           size:50,
           image: require("../assets/img/computer.png"),
           shape:"image",
           title: ""
          },
          {id:'gdcni16',
           label:'gdcni16'+'\n'+'',
           level:2,
           font:{size:20},
           size:50,
           image: require("../assets/img/computer.png"),
           shape:"image",
           title: ""
          }
      ]);
      
//createanarraywithedges
varedges=newDataSet([
{from:'IP ',to:'gdcni18'},
{from:'gdcni18',to:'gdcni12'},
          {from:'gdcni18',to:'gdcni16'}
]);

//createanetwork
varcontainer=document.querySelector('#visualization');

//providethedatainthevisformat
vardata={
nodes:nodes,
edges:edges
};
varoptions={
          nodes: {
          borderWidth: 3
         },
         edges: {
          color: 'lightgray'
        },
          physics:{
            enabled:true,
          },
          layout:{
            hierarchical:{
              direction:"UD",
            },
          },
          interaction: { hover: true, tooltipDelay: 300 }
      };
      
//initializeyournetwork!
this.network=newNetwork(container,data,options);
      this.network.setSize('1200','600');   //canvas
      this.network.enableEditMode()
      this.network.on("selectNode", this.handleClick);
      this.network.on("hoverNode", function (params) {
        console.log("hoverNode Event:", params);
      });
      this.network.on("blurNode", function (params) {
        console.log("blurNode Event:", params);
      });
}
}
}
</script>


<style scoped>
.vis-network{
    position: fixed;
    overflow: visible;
    touch-action: pan-y;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    width: 100%;
    height: 100%;
}

</style>
 
