<template>
    <div class="fillcontain">
        <div>
            <el-form :inline="true" ref="add_data" >
                <el-form-item label=":">
                    <el-date-picker v-model="search_data.startTime" type="datetime" placeholder=""></el-date-picker>--
                    <el-date-picker v-model="search_data.endTime" type="datetime" placeholder=""></el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" size="small" icon="search" @click="onScreeoutMoney()"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="table_container">
            <el-table
            v-if="tableData.length > 0"
            :data="tableData"
            max-height="450"
            border
            :default-sort="{prop: 'id', order: 'descending'}"
            style="width: 100%"
            >
                <el-table-column prop="id" label="" align="center" width="80"></el-table-column>
                <el-table-column prop="timestamp" label="" align="center" width="200" sortable>
                <template slot-scope="scope">
                    <el-icon name="time"></el-icon>
                    <span style="margin-left: 10px">{{ time(scope.row.timestamp)}}</span>
                </template>
                </el-table-column>
                <el-table-column prop="hostname" label="" align="center" width="100"></el-table-column>
                <el-table-column prop="name" label="" align="center" width="300"></el-table-column>
                <el-table-column prop="level" label="" align="center" width="80"></el-table-column>
                <el-table-column prop="cat_name" label="" align="center" width="300"></el-table-column>
                <el-table-column prop="operation" align="center" label="" fixed="right" width="240">
                  <template slot-scope="scope">
                    <el-button type="warning" icon="edit" size="small"
                    @click="showDetail(scope.$index,scope.row)"></el-button>
                    <el-button type="danger" icon="delete" size="small"
                    @click="onDelete(scope.$index,scope.row)"></el-button>
                </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-row>
                <el-col :span="24">
                <div class="pagination">
                    <el-pagination
                    v-if="paginations.total > 0"
                    :page-sizes="paginations.page_sizes"
                    :page-size="paginations.page_size"
                    :layout="paginations.layout"
                    :total="paginations.total"
                    :current-page.sync="paginations.page_index"
                    @current-change="handleCurrentChange"
                    @size-change="handleSizeChange"
                    ></el-pagination>
                </div>
                </el-col>
            </el-row>
        </div> 
        <el-dialog
                 title=""
                 :visible.sync="centerDialogVisible"
                 width="50%"
                 center>
                 <span>{{this.full_log}}</span>
                 <span slot="footer" class="dialog-footer">
                 <el-button @click="centerDialogVisible = false"> </el-button>
                 <el-button type="primary" @click="centerDialogVisible = false"> </el-button>
                </span>
      </el-dialog> 
    </div>

</template>
<script>

export default {
    name:'fundList',
    data(){
        return{
            tableData:[], 
            alltableData:[],
            filterTableData: [],  
            form:{
                id: "",
                hostname: "",
                name:"",
                level: "",
                full_log:"",
                timestamp:"",
                cat_name:""
            },
            centerDialogVisible: false,
            //
            paginations: {
                page_index: 1, // 
                total: 0, // 
                page_size: 10, // 1
                page_sizes: [10 , 15, 20, 25], //
                layout: "total, sizes, prev, pager, next, jumper" // 
            },
            //
            search_data: {
                startTime: "",
                endTime: ""
            }
        };
    },
    computed:{
        user() {
          return this.$store.getters.user;
    }
    },
    created(){
        this.getAlert();
    },
    methods:{
        getAlert(){
            this.$axios.get('/api/alerts').then(res => {
            console.log(res);
            this.tableData = res.data;
            this.allTableData = res.data;
            this.filterTableData = res.data;
            // // 
            this.setPaginations();
            }).catch(err => console.log(err));
        },        
        onDelete(index,row){
            // 
            this.$axios.post(`/api/alerts/delete`,{id:row.id}).then(res => {
            this.$message("");
            this.getAlert();
            });
        },
        showDetail(index,row){
            this.centerDialogVisible =true;
            this.full_log = row.full_log;
            return this.full_log;          
        },
        setPaginations() {
            // 
            this.paginations.total = this.allTableData.length;
            this.paginations.page_index = 1;
            this.paginations.page_size = 10;
            // 
            this.tableData = this.allTableData.filter((item, index) => {
                return index < this.paginations.page_size;
            });
        },
        handleSizeChange(page_size) {
            // size
            this.paginations.page_index = 1;
            this.paginations.page_size = page_size;
            this.tableData = this.allTableData.filter((item, index) => {
                return index < page_size;
            });
        },
        // 
        handleCurrentChange(page) {
            // 
            let sortnum = this.paginations.page_size * (page - 1);
            // 
            let table = this.allTableData.filter((item, index) => {
                return index >= sortnum;
            });
            // 
            this.tableData = table.filter((item, index) => {
                return index < this.paginations.page_size;
            });
        },
                // 
        onScreeoutMoney() {
            // 
            if (!this.search_data.startTime || !this.search_data.endTime) {
                this.$message({
                    type: "warning",
                    message: ""
                });
                this.getAlert();
                return;
            }
            const stime = this.search_data.startTime.getTime();
            const etime = this.search_data.endTime.getTime();
            this.allTableData = this.filterTableData.filter(item => {
                let date = new Date(item.timestamp*1000);
                let time = date.getTime();
                return time >= stime && time <= etime;
            });
            // 
            this.setPaginations();
        },
        //
        time(timestamp) {
vardate =newDate(timestamp * 1000);//10*1000131000
    varY = date.getFullYear() +'-';
varM = (date.getMonth()+1 <= 10 ?'0'+(date.getMonth()+1) : date.getMonth()+1) +'-';
varD = (date.getDate()+1 <= 10 ?'0'+(date.getDate()) : date.getDate())+' ' ;
varh = (date.getHours()+1 <= 10 ?'0'+(date.getHours() ) : date.getHours())+':';
varm = (date.getMinutes()+1 <= 10 ?'0'+(date.getMinutes() ) : date.getMinutes() )+':';
vars = (date.getSeconds()+1 <= 10 ?'0'+(date.getSeconds() ) : date.getSeconds());
returnY+M+D+h+m+s;

}
    },
};
</script>

<style scoped>
.fillcontain {
  width: 100%;
  height: 100%;
  padding: 16px;
  box-sizing: border-box;
}
.btnRight {
  float: right;
}
.pagination {
  text-align: right;
  margin-top: 10px;
}
</style>